input, textarea {
  font-size: 16px;
  line-height: 24px;
  color: var(--color-foreground);
  appearance: none;
  background-color: rgba(var(--color-lightpink-rgb),0.3);
  border: none;
  padding: 8px;
  width: 100%;
}

select {
  // A reset of styles, including removing the default dropdown arrow
  appearance: none;
  background-color: transparent;
  background-image: url("data:image/svg+xml,%3Csvg class='icon icon-caret' width='9' height='6' viewBox='0 0 9 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.24264 5.48529L3.53553 4.77818L7.77817 0.535543L8.48528 1.24265L4.24264 5.48529Z' fill='currentColor'%3E%3C/path%3E%3Cpath d='M3.53553 4.77818L0 1.24265L0.707107 0.535543L4.24264 4.07108L3.53553 4.77818Z' fill='currentColor'%3E%3C/path%3E%3C/svg%3E");
  background-position: right center;
  background-repeat: no-repeat;
  border: none;
  padding: 8px 24px 8px 0;
  margin: 0;
  width: 100%;
  color: var(--color-foreground);
  font-family: inherit;
  font-size: inherit;
  cursor: inherit;
  line-height: inherit;
  font-size: 100%;
  line-height: 24px;
  outline: none;
  width: 100%;
}

label {
  // font-weight: bold;
  // text-transform: uppercase;
  font-size: 100%;
  line-height: 24px;
  display: block;
}

fieldset {
  border: 0;
  margin: 0;
}

.contact img {
  max-width: 100%;
}

.contact .form__message {
  align-items: flex-start;
}

.contact .icon-success {
  margin-top: 0.2rem;
}

.contact .field {
  margin-bottom: 1.5rem;
}

@media screen and (min-width: 750px) {
  .contact .field {
    margin-bottom: 2rem;
  }
}

.contact__button {
  margin-top: 3rem;
  @media screen and (max-width: 480px) {
    button {
      width: 100%;
    }
  }
}
.contact__fields {
  margin-top: 24px;  
}

@media screen and (min-width: 750px) {
  .contact__button {
    margin-top: 4rem;
  }
}

.grecaptcha-badge {
  visibility: hidden;
}


.field {
  margin-bottom: 12px;
  &.flex {
    label {
      flex: 1 0 30%;
    }
    input, textarea {
      flex: 0 1 65%;
    }
  }
}