.image-with-text {
  margin-top: 40px;
  img {
    object-fit: cover;
    display: block;
  }
  @media screen and (min-width: 880px) {
    gap: 32px;
  }
  .image {
    grid-column: span 3;
    position: relative;
    @media screen and (min-width: 880px) {
      grid-column: 1 / 3;
      grid-row: 1/1;
    }
  }
  .text {
    height: 100%;
    .button {
      @media screen and (max-width: 480px) {
        width: 100%;
      }
    }
    .flex {
      height: 100%;
      align-items: center;
      align-content: center;
      justify-content: flex-start;
    }
    h2 {
      margin: 16px 0 8px;
      @media screen and (min-width: 768px) {
        margin: 0;
        margin-bottom: 8px;
      }
    }
    grid-column: span 3;
    @media screen and (min-width: 880px) {
      grid-column: 3 / 3;
      grid-row: 1/1;
    }
  }
  &.switch {
    .text {
      @media screen and (min-width: 880px) {
        grid-column: 1 / 2;
      }
    }
    .image {
      @media screen and (min-width: 880px) {
        grid-column: 2 / 4;
      }
    }
  }
}

.text_wrap:not(.under) {
  position: absolute;
  top: 16px;
  left: 16px;
  right: 16px;
  bottom: 16px;
  z-index: 2;
  @media screen and (min-width: 880px) {
    top: 32px;
    left: 32px;
    right: 32px;
    bottom: 32px;
  }
}
.text_wrap {
  z-index: 2;
  h2, h3, .h2, .h3 {
    font-weight: 700;
  }
  span {
    flex:  1 1 100%;
  }
}