//predictive search results
.search_close {
  background-color: rgba(255,255,255,0.0);
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  position: fixed;
  left: 0;
  top: 0;
  height: 0px;
}
.predictivesearchresults {
  position: fixed;
  display: none;
  opacity: 0;
  visibility: hidden;
  text-align: left;
  transition: var(--transition-slow);
  max-width: 550px;
  width: 320px;
  right: -320px;
  z-index: -1;
  height: calc(100vh - 56px);
  background: transparent;
  color: white;
  padding: 0;
  border-radius: 0;
  top: 56px;
  overflow-x: scroll;
  .incider {
    padding: 8px 16px 16px 16px;
    background: var(--color-green);
    min-height: 100%;
    margin-left: 6px;
    position: relative;
    &:after {
      content: " ";
      position: absolute;
      width: 6px;
      display: block;
      right: 100%;
      height: 100%;
      top: 0;
      background-image: url("data:image/svg+xml,%3Csvg width='7' height='14' viewBox='0 0 7 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.908447 6.99997L6.90845 13.9199L6.90845 0.0800171L0.908447 6.99997Z' fill='%238DC587'/%3E%3C/svg%3E%0A");
      background-position: top left;
      background-repeat: repeat-y;
    }
  }
  @media screen and (min-width: 374px) {
    width: 375px;
    right: -375px;
  }
  @media screen and (min-width: 550px) {
    width: 550px;
    right: -550px;
  }
  @media screen and (min-width: 600px) {
    top: 56px;
  }
  &.alive {
    display: block;
    visibility: visible;
  }
  &.active {
    opacity: 1;
    z-index: 99999;
    right: 0;
    @media screen and (min-width: 1440px) {
      // left: 50%;
      // transform: translateX(-50%);
      // width: 100%;
    }
    &+.search_close {
      height: 100vh;
      width: 100vw;
      z-index: 99998;
      cursor: default;
    }
  }
  h4 {
    flex-basis: 100%;
    text-transform: none;
    font-weight: 600;
    min-width: 100%;
    font-size: 100%;
    color: white;
    padding: 8px 0;
    border-bottom: 1px solid white;
    margin-bottom: 4px;
    font-family: var(--font-body-family);
  }
  a {
    font-size: 100%;
    color: white;
    display: block;
    font-family: var(--font-body-family);
    padding: 8px 0;
    img {
      width: 56px;
      height: 56px;
      @media screen and (min-width: 600px) {
        width: 96px;
        height: 96px;
      }
    }
    &.more_results {
      padding: 6px 0 8px 0;
    }
  }
  .searchProducts {
    position: relative;
    z-index: 2;
    padding-top: 16px;
    strong {
      display: block;
      font-family: var(--font-heading-family);
      font-weight: 400;
    }
    display: flex;
    flex-direction: row; 
    flex-wrap: wrap; 
    justify-content: flex-start; 
    align-items: stretch; 
    align-content: flex-start;
    gap: 12px;
    @media screen and (min-width: 600px) {
      gap: 24px;
    }
    a {
      flex: 1 1 50%;
      flex-basis: calc(50% - 24px);
      max-width: calc(50% - 24px);
      display: flex;
      flex-direction: row; 
      flex-wrap: nowrap; 
      justify-content: flex-start; 
      align-items: center; 
      align-content: center;
      gap: 8px;
      transition: var(--transition);
      &.more_results {
        flex: 1 0 100%;
        padding: 8px 0;
        max-width: 100%;
      }
      &:hover {
        background: rgba(255,255,255,0.1);
      }
      @media screen and (min-width: 600px) {
        flex-basis: calc(50% - 24px);
        max-width: calc(50% - 24px);
      }
    }
  }
  .searchContent {
    position: relative;
    z-index: 2;
    a {
      display: inline-block;
      margin-right: 8px;
      font-family: var(--font-heading-family);
      font-weight: 400;
      &.more_results {
        font-family: var(--font-body-family);
      }
    }
    @media screen and (min-width: 600px) {
      grid-column: 1 / span 1;
      a {
        display: block;
        margin-right: 0;
      }
    }
    h4 {
      @media screen and (max-width: 600px) {
        padding-top: 12px;
      }
    }
  }
  .searchPages, .searchArticles, .searchCollections {
    padding-top: 16px;
  }
}
.header_search.active {
  > .form_inner {
    z-index: 99999;
  }
}
@media screen and (max-width: 600px) {
  body.search-active .app {
    overflow: hidden;
    position: fixed;
  }
}
.predictivesearchresults {
  input[type="text"] {
    background-color: var(--color-green);
    color: white;
    border: 1px solid rgba(255,255,255,0.5);
    width: 100%;
    &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
      color: white;
    }
    &::-moz-placeholder { /* Firefox 19+ */
      color: white;
    }
    &:-ms-input-placeholder { /* IE 10+ */
      color: white;
    }
    &:-moz-placeholder { /* Firefox 18- */
      color: white;
    }
  }
}