.featured_blog-section, .featured_collection-section {
  position: relative;
  overflow: hidden;
  margin-top: 0;
  padding: 0 0 16px 0;
  padding-left: 8px;
  @media screen and (min-width: 768px) {
    margin-top: 24px;
    padding: 32px 0;
    padding-left: 16px;
  }
  @media screen and (min-width: 1024px) {
    margin-top: 40px;
    padding: 50px 0;
    padding-left: 24px;
  }
}
.background_img {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 0;
  right: 0;
  bottom:  0;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.card {
  flex: 1 0 300px;
  width: 300px;
  max-width: 300px;
  max-height: 400px;
  position: relative;
  overflow: hidden;
  @media screen and (min-width: 768px) {
    flex-basis: 406px;
    width: 406px;
    max-width: 406px;
  }
  &.product_card {
    flex-basis: 277px;
    width: 277px;
    max-width: 277px;
  }
  a {
    color: var(--color-dark);
  }
  &.main_card {
    flex: 1 0 200px;
    min-width: 200px;
    align-self: center;
    @media screen and (min-width: 768px) {
      flex-basis: 380px;
      min-width: 380px;
      height: 684px;
    }
    @media screen and (min-width: 1024px) {
      flex-basis: 700px;
      min-width: 700px;
      height: 684px;
    }
    h2, div {
      max-width: 494px;
      margin: 0 auto;
      color: var(--color-dark);
    }
  }
  picture {
    display: block;
    position: relative;
    z-index: 1;
    img {
      display: block;
    }
  }
  header {
    position: absolute;
    z-index: 2;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 16px;
  }
  &:nth-child(3n+1) {
    header {
      background-color: rgba(var(--color-yellow-rgb), 0.8);
    }
  }
  &:nth-child(3n+2) {
    header {
      background-color: rgba(var(--color-pink-rgb), 0.8);
    }
  }
  &:nth-child(3n+3) {
    header {
      background-color: rgba(var(--color-red-rgb), 0.8);
    }
  }
}

.featured_blog-section .featured_blog, .featured_collection-section .featured_collection {
  scrollbar-color: #666 #201c29;
  -webkit-overflow-scrolling: touch;
  display: block;
  overflow-x: scroll;
  position: relative;
  z-index: 2;
}

.featured_blog-section .featured_blog::-webkit-scrollbar, 
.featured_collection-section .featured_collection::-webkit-scrollbar {
  width: 2px;
  height: 2px;
  background-color: white; /* or add it to the track */
}

.featured_blog-section .featured_blog::-webkit-scrollbar-thumb, 
.featured_collection-section .featured_collection::-webkit-scrollbar-thumb {
  background: var(--color-red);
  border-radius: 1px;
}

.featured_blog-section .featured_blog::-webkit-scrollbar-track, 
.featured_collection-section .featured_collection::-webkit-scrollbar-track {
  background: white;
}
