// uses instagram section
.press-section {
  .instagramification {
    gap: 8px;
    @media screen and (min-width: 768px) {
      gap: 16px;
    }
    @media screen and (min-width: 1440px) {
      gap: 24px;
    }
    .block {
      padding: 24px;
      background-color: white;
      border: 1px solid #8dc68775;
      color: var(--color-dark);
      align-content: center;
      a {
        flex: 1 1 100%;
      }
    }
  }
}