.insta-section {
  margin: 40px 0 40px 0;
  position: relative;
  .button {
    margin-top: 24px;
    @media screen and (min-width: 600px) {
      margin-top: 32px;
    }
  }
  .insta {
    //padding: 48px 0 56px;
    position: relative;
    z-index: 2;
    opacity: 1;
  }
  h2, p.subtitle {
    text-align: center;
  }
  p.subtitle {
    font-size: 106.25%;
    margin-bottom: 32px;
  }
  .instagramification {
    grid-template-columns: 1fr 1fr;
    gap: 0;
    @media screen and (min-width: 600px) {
      grid-template-columns: 1fr 1fr 1fr;
    }
    @media screen and (min-width: 960px) {
      grid-template-columns: 1fr 1fr 1fr 1fr;
    }
    .block {
      padding: 0;
      margin: 0;
      position: relative;
      
      a {
        display: block;
      }
      img {
        display: block;
        transition: opacity 0.3s ease;
      }
      .cite {
        font-style: normal;
        position: absolute;
        left: 0;
        bottom: 0;
        right: 0;
        padding: 8px;
        text-align: center;
        .hover & {
          transition: opacity 0.3s ease;
          opacity: 0;
        }
        a {
          color: var(--color-dark);
        }
      }
      .hover & {
        &:hover {
          .cite {
            opacity: 1;
          } 
        }
      }
      &:nth-child(5n+1) {
        .cite {
          background-color: rgba(var(--color-red-rgb), 0.8);
        }
      }
      &:nth-child(5n+2) {
        .cite {
          background-color: rgba(var(--color-pink-rgb), 0.8);
        }
      }
      &:nth-child(5n+3) {
        .cite {
          background-color: rgba(var(--color-lightpink-rgb), 0.8);
        }
      }
      &:nth-child(5n+4) {
        .cite {
          background-color: rgba(var(--color-yellow-rgb), 0.8);
        }
      }
      &:nth-child(5n+5) {
        .cite {
          background-color: rgba(var(--color-green-rgb), 0.8);
        }
      }
    }
  }
}
