.text_and_icons-section {
  margin: 80px 0;
}


.text_and_icons {
  ul {
    list-style-type: none;
    margin: 0;
    padding: 24px 0;
    @media screen and (min-width: 768px) {
      padding: 40px 0;
    }
  }
  &.background-lines {
    ul {
      border-top: 1px solid var(--color-foreground);
      border-bottom: 1px solid var(--color-foreground);
    }
  }
}

.ti_column {
  flex: 1 1 100%;
  @media screen and (min-width: 480px) {
    flex-basis: 200px;
  }
  @media screen and (min-width: 768px) {
    flex-basis: 340px;
  }
  h4 {
    margin-bottom: 16px;
    @media screen and (min-width: 768px) {
      margin-bottom: 24px;
    }
    &.sans {
      font-size: 125%;
    }
  }
  .column_card {
    position: relative;
  }
  a {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
  }
}